import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { policyObjects } from 'dump/data';

const columns = [
  {
    title: 'Ký hiệu',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Tên đối tượng',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Miễn giảm (%)',
    dataIndex: 'maxScores',
    key: 'maxScores',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      policyObjects.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#policy-objects/form/edit/${record.code}`} />
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class ListPolicyObjectPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Đối tượng chính sách">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/policy-objects/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={policyObjects} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListPolicyObjectPage />
  </Page>
);



